import * as React from "react";
import * as ReactDOM from "react-dom";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

import Home from "./components/screens/Home";
import FormScreen from "./components/screens/FormScreen";
import DogScreen from "./components/screens/DogScreen";

import NavigationMenu from "./components/NavigationMenu";
import Button from "./components/Button";

ReactDOM.render(
  <Router>
    <div className="screen">
      <NavigationMenu>
        <Button to="/" className="fadeIn">Home</Button>
        <Button to="/form" className="fadeIn">Take the test</Button>
        <Button to="/dogscreen" className="fadeIn">More dog breeds</Button>
      </NavigationMenu>

      {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
      <Switch>
        <Route path="/form">
          <FormScreen />
        </Route>
        <Route path="/dogscreen">
          <DogScreen />
        </Route>
        <Route path="/">
          <Home />
        </Route>
      </Switch>
    </div>
  </Router>,

  document.getElementById("root")
);
